.imgFamille {
    background-image: url(../../../../public/assets/moodday/famille.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 0%;
    box-shadow: 1px 2px 15px 4px rgba(5, 5, 5, 0.301);
}

h1 {
    text-align: center;
    font-family: "Poppins";
    color: #8a1c1c;
}

@media all and(max-width: 840px) {
    .imgMaison,
    .imgFetard,
    .imgAmoureux,
    .imgAmis,
    .imgFamille {
        width: 90px;
        height: 90px;
    }
}
